.dropdown-menu {
    width: 200px;
    position: absolute;
    top: 60px;
    list-style: none;
    text-align: start;
}

.dropdown-menu li {
    background : #1888ff;
    cursor: pointer;
}

.dropdown-menu li:hover{
    background: #5cabff;
}


.dropdown-link{
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
}

@media screen and (max-width: 960px){
    .fa-caret-down {
        display: none;
    }
}